:root {
  --db-meter-sound-delay: 2s;
  --db-meter-buzz-duration: 2s;
}

.db-meter {
  height: 10rem;
  animation-name: db-meter-light-on;
  animation-duration: 4s;
  animation-fill-mode: forwards;
  animation-delay: var(--clock-plate-radio-delay);
  background-color: #f5f5f5;
  justify-content: center;
  align-items: flex-end;
  display: flex;
  overflow: hidden;
  box-shadow: inset 0 0 5px #0000004d, inset 4px 4px 5px #0006;
}

.db-meter__scale {
  width: 80%;
  height: 80%;
  position: relative;
}

.db-meter__grade, .db-meter__needle {
  background-color: var(--color);
  transform-origin: 50% 24rem;
  width: 1px;
  height: 1.6rem;
  margin: 2rem auto 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.db-meter__grade:first-child {
  transform: rotate(-22deg);
}

.db-meter__grade:first-child .db-meter__grade-amount {
  transform: translateX(-50%)rotate(22deg);
}

.db-meter__grade:nth-child(2) {
  transform: rotate(-17deg);
}

.db-meter__grade:nth-child(2) .db-meter__grade-amount {
  transform: translateX(-50%)rotate(17deg);
}

.db-meter__grade:nth-child(3) {
  transform: rotate(-12deg);
}

.db-meter__grade:nth-child(3) .db-meter__grade-amount {
  transform: translateX(-50%)rotate(12deg);
}

.db-meter__grade:nth-child(4) {
  transform: rotate(-9.5deg);
}

.db-meter__grade:nth-child(4) .db-meter__grade-amount {
  transform: translateX(-50%)rotate(9.5deg);
}

.db-meter__grade:nth-child(5) {
  transform: rotate(-7deg);
}

.db-meter__grade:nth-child(5) .db-meter__grade-amount {
  transform: translateX(-50%)rotate(7deg);
}

.db-meter__grade:nth-child(6) {
  transform: rotate(-5deg);
}

.db-meter__grade:nth-child(6) .db-meter__grade-amount {
  transform: translateX(-50%)rotate(5deg);
}

.db-meter__grade:nth-child(7) {
  transform: rotate(-3deg);
}

.db-meter__grade:nth-child(7) .db-meter__grade-amount {
  transform: translateX(-50%)rotate(3deg);
}

.db-meter__grade:nth-child(8) {
